import axios from 'axios'
import { Loading } from 'element-ui' //  Element UI  Loading 
import store from '@/store' //  Vuex store
import router from '@/router' //  Vue Router
import { getInfo, removeInfo } from '@/utils/storage'

//  URL
// const getBaseURL = () => {
//   const hostname = window.location.hostname

//   if (hostname === 'fohufo.com/fo') {
//     return 'https://fohufo.com/fo'
//   } else if (hostname === 'test.fohufo.com') {
//     return 'https://test.fohufo.com/fo'
//   } else {
//     return 'https://default.example.com' //  URL 
//   }
// }

const instance = axios.create({
  baseURL: 'https://fohufo.com/fo', //  baseURL
  timeout: 7000 // 
})

let loadingInstance = null
let isRefreshing = false
let requestsQueue = []

// 
instance.interceptors.request.use(
  function (config) {
    loadingInstance = Loading.service({
      text: 'Loading...',
      fullscreen: true,
      background: 'rgba(0, 0, 0, 0.7)'
    })

    const token = store.state.user.token
    if (token) {
      config.headers.platform = 'fo' // 
      config.headers.Authorization = `${token}`
    }
    return config
  },
  function (error) {
    if (loadingInstance) loadingInstance.close()
    return Promise.reject(error)
  }
)

// 
instance.interceptors.response.use(
  function (response) {
    if (loadingInstance) loadingInstance.close()
    return response.data
  },
  async function (error) {
    if (loadingInstance) loadingInstance.close()

    const originalRequest = error.config

    if (error.response && error.response.status === 401) {
      if (!isRefreshing) {
        isRefreshing = true

        try {
          const { refreshToken } = getInfo() //  refreshToken
          const response = await axios.post('/user/refreshToken', {
            clientId: 'fohufo_app',
            loginType: 1,
            refreshToken
          })
          console.log('', response)

          const newToken = response.data.access_token
          store.commit('ecommerceft_info', newToken) //  token
          originalRequest.headers.Authorization = newToken

          requestsQueue.forEach(cb => cb(newToken))
          requestsQueue = []
          return instance(originalRequest)
        } catch (refreshError) {
          removeInfo() // 
          router.push('/login')
          return Promise.reject(refreshError)
        } finally {
          isRefreshing = false
        }
      } else {
        return new Promise(resolve => {
          requestsQueue.push(token => {
            originalRequest.headers.Authorization = token
            resolve(instance(originalRequest))
          })
        })
      }
    }

    return Promise.reject(error)
  }
)

export default instance
