  width: 100%; /*  */
  height: 780px;
  background-image: url('@/img/login/login.png');
  background-size: cover; /*  */
  background-position: center; /*  */
}

/*  */
.loginform {
  margin-right: 200px;
  width: 550px;
  height: 610px;
  background-color: white;
  border-radius: 10px;
