  width: 100%; /*  */
}

.login-button, .register-button {
  border-radius: 20px;
  width: 100%; /*  */
}

.login-button {
  background-color: black; /*  */
  color: white; /*  */
  border-color: black; /*  */
}

.login-button:hover {
  background-color: rgba(16, 50, 93); /*  */
