}

/*  */
.CopyrightInformation {
  width: 98.8%;
  text-align: center;
  padding: 20px 0;
  box-sizing: border-box;
}

.CopyrightInformation p {
  margin: 0;
  font-size: 14px;
  color: #979899;
  margin-bottom: 10px;
}

