<template>
  <div>
    <button @click="goBack">return</button>
    <h1 class="main-title">Case VR display</h1>
    <h2 class="sub-title">You can click on the display image below to view the corresponding VR effect</h2>
    <div class="showCanvas" id="viewer"></div>
    <div class="vrimglist">
      <div class="scroll-button" @click="scrollLeft">{{'<'}}</div>
      <div class="scroll-container" ref="scrollContainer">
        <div class="item" v-for="(i,index) in items" :key="index" @click="getimgurl(i)">
          <div class="item-content">
            <div class="item-image">
              <img :src="i.remark" alt="" />
            </div>
            <div class="item-description">{{i.sceneName}}</div>
          </div>
        </div>
      </div>
      <div class="scroll-button" @click="scrollRight"> ></div>
    </div>
  </div>
</template>

<script>
import { Viewer } from 'photo-sphere-viewer'
import 'photo-sphere-viewer/dist/photo-sphere-viewer.css'
import { SelVRlist } from '@/api/VR/VR'
export default {
  name: 'CommodityIndex',
  data () {
    return {
      viewer: null,
      img: require('@/img/VR/3.jpeg'),
      items: []// 120
    }
  },
  mounted () {
    this.SelVRlist()
    this.viewer = new Viewer({
      container: document.querySelector('#viewer'),
      panorama: this.img,
      size: {
        width: '60vw',
        height: '60vh'
      }
    })
  },
  created () {
    console.log(this.items)
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    },
    scrollLeft () {
      const container = this.$refs.scrollContainer
      if (container) {
        container.scrollBy({ left: -200, behavior: 'smooth' })
      }
    },
    scrollRight () {
      const container = this.$refs.scrollContainer
      if (container) {
        container.scrollBy({ left: 200, behavior: 'smooth' })
      }
    },
    async SelVRlist () {
      const res = await SelVRlist()
      console.log(res)
      this.items = res.data
    },
    getimgurl (i) {
      // console.log(i)
      this.img = i.remark
      this.viewer.setPanorama(this.img) //  Viewer 
    }
  }
}
</script>

<style scoped>
.item-description {
  font-size: 14px; /*  */
  color: #333; /*  */
  text-align: center; /*  */
  word-wrap: break-word; /*  */
  overflow-wrap: break-word; /*  */
  white-space: normal; /*  */
}

.main-title {
  text-align: center; /*  */
  font-size: 24px; /*  */
  margin: 20px 0; /*  */
}

.sub-title {
  text-align: center; /*  */
  font-size: 16px; /*  */
  color: #555; /*  */
  margin-bottom: 20px; /*  */
}
.showCanvas {
  margin: 20px auto;
  width: 800px;
  height: 500px;
}
button {
  margin-bottom: 10px;
  width: 100px;
  height: 30px;
  line-height: 30px;
  border: none;
  color: white;
  background-color: black;
}
.vrimglist {
  display: flex;
  margin: auto;
  align-items: center;
  width: 60%;
  overflow: hidden;
}
.scroll-container {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  white-space: nowrap;
  scrollbar-width: none;
}
.scroll-container::-webkit-scrollbar {
  display: none;
}
.item {
  flex: 0 0 auto;
  width: 200px;
  margin: 10px;
}
.item-content {
  display: flex;
  flex-direction: column; /*  */
  align-items: center;
  background-color: rgb(253, 255, 255);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /*  */
  padding: 10px;
  border-radius: 5px;
}
.item-image img {
  width: 100%; /* 100% */
  height: auto; /*  */
  border-radius: 5px; /*  */
}
.item-description {
  font-size: 14px; /*  */
  color: #333; /*  */
  text-align: center; /*  */
}
.scroll-button {
  /* background-color: grey; */
  color: rgb(12, 12, 12);
  border: none;
  cursor: pointer;
  font-size: 30px;
}
/*  */
@media (max-width: 768px) {
  button {
  margin-bottom: 10px;
  width: 200px;
  height: 80px;
  line-height: 50px;
  border: none;
  color: white;
  background-color: black;
}
  .showCanvas {
    width: 100%; /* 100% */
    height: 300px; /*  */
  }

  .item {
    width: 340px; /*  */
    margin: 5px; /*  */
  }

  .item-description {
    font-size: 12px; /*  */
  }

  .scroll-button {
    width: 60px; /*  */
    height: 45px; /*  */
  }
}

@media (max-width: 480px) {
  button {
  margin-bottom: 10px;
  width: 300px;
  height: 120px;
  line-height: 120px;
  border: none;
  color: white;
  background-color: black;
}
  .item {
    width: 300px; /*  */
  }

  .item-description {
    font-size: 10px; /*  */
  }

  .scroll-button {
    width: 50px; /*  */
    height: 50px; /*  */
    line-height: 50px;
  }
}
</style>
